.body {
    display: grid;
    grid-template-columns: auto auto;
    max-width: 80%;
    grid-row-gap: 15px;
}
.upper-body {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
}

.img-card,
img {
    height: 145px;
    object-fit: cover;
    width: 220px;

    margin: 0px;
   
}
.img-card{
    
    border: 4px solid #c59d5f;
    border-radius: 5px;

}

.food-card {
    /* width: 85%; */
    display: flex;
    /* justify-content: space-between; */
    margin: 15px;
}

.info-card h3 {
    
    display: inline;
    margin: 0px;
    text-transform: capitalize;
    color: #c59d5f;
}

.info-card h4 {
    
    display: inline;
    margin: 0px;
    color: #c59d5f;
}

.info-card {
    /* width: 60%;     */
    margin-left: 15px;

     
}
.header {
    color: black;
    margin: 61px 0px 0px 0px;
    font-weight: 900;
    font-size: 45px;
    text-align: center;
}
.underline {
    height: 4px;
    background-color: #c59d5f;
    width: 100px;
    margin: 5px 0px 30px 0px;
}

.info-cardp{
    color: gray;
}

.info-card-top {
    display: flex;
    justify-content: space-between;
    border-bottom: 2px dotted #617d98;
    letter-spacing: 2px;
}
.info-card-bottom{
    letter-spacing: 1px;
    color: #617d98;
    
}
.filter-btn {
    color: #c59d5f;
    font-size: 17px;
    margin: 11px;
    background-color: #f1f5f8;
    cursor: pointer; 
    border: none;
    letter-spacing: 1px;
    text-transform: capitalize;
    border-radius: 5px;
    padding: 8px 10px 8px 10px;
}
.btn-section{
    margin-bottom: 30px;
    
}
/* 
text-transform: capitalize;
*/
.filter-btn:hover{
    color: white;
    background-color: #c59d5f;
}

/* making it responsive */
@media screen and (max-width: 1050px) {
    .body{

        max-width: -webkit-fill-available;
    
    }
    
}
@media screen and (max-width: 1000px) {
    .body{

        display:block;
    
    }
    .food-card {
     margin-bottom: 50px;
    }
  
}


@media screen and (max-width: 550px) {
    .img-card, img {
        height: 220px;
        margin-bottom: 20px;
        width: -webkit-fill-available;
        
    }
    .food-card {
        
        flex-direction: column;
        margin: 0px 30px 50px 30px;
    }
    .filter-btn{
        margin: 3px;
    }
    .body {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

   
    
} 